import React from 'react';
import classes from './Input.css';

const Input = ({type,value,placeholder,style,change}) =>(
    <input 
    style={style?style:{}}
    className={classes.Inp?classes.Inp:''} 
    type={type} 
    placeholder={placeholder} 
    value={value}
    onChange = {change}   
    />
);


export default Input