import React, { useState } from "react"; 
import Resource from "./resource";
import PersonalDetailsOne from './onboarding/pageone'

const ShowcaseTodo = ({ style }) => {
  const [count, setCount] = useState(0);

  const incrementState = ()=> {
    return setCount(count + 1)
  };

  if (count === 0) {

    return (
      <div>
        <div className={style.todoDiv}>
          <div className={style.todohead}>
            <p>To Do List</p>
          </div>
          <div className={style.todoForm}>
            <form>
              <div className={style.formStyle}>
                <div className={style.todoChk}>
                  <input type="checkbox" name="profile" id="profile" />
                </div>
                <label for="profile">Complete your employee profile</label>
              </div>
              <div className={style.formStyle}>
                <div className={style.todoChk}>
                  <input
                    type="checkbox"
                    name="induction"
                    id="induction"
                    value=""
                  />
                </div>
                <label for="induction">Watch company induction video</label>
              </div>
              <div className={style.formStyle}>
                <div className={style.todoChk}>
                  <input type="checkbox" name="team" id="team" value="" />
                </div>
                <label for="team">Meet the team</label>
              </div>
              <div className={style.formStyle}>
                <div className={style.todoChk}>
                  <input type="checkbox" name="todo" id="todo" value="" />
                </div>
                <label for="todo">To Do 4</label>
              </div>
              <div>
                <input
                  className={style.todoSubmit}
                  type="submit"
                  name="submit"
                  value="Submit"
                  onClick={incrementState}
                />
              </div>
            </form>
          </div>
        </div>
        <Resource style={style}/>
      </div>
    );
  }
  if (count === 1) {
    return <PersonalDetailsOne style={style} />
  }
};

export default ShowcaseTodo;
