import React, { useState } from "react"; 
import { Link } from "react-router-dom";
import ShowcaseTodo  from './todo'

const Showcase = ({style}) => {
  const [count, setCount] = useState(0);

  const incrementState = ()=> {
    return setCount(count + 1)
  };

  if (count === 0) {
    return (
      <div className={style.showcase}>
        <div className={style.showcaseCompany}>
          <p>[Company Name]</p>
        </div>
        <div className={style.showcaseWelcome}>
          <p>Welcome to the team, William</p>
        </div>
        <div className={style.showcaseHelp}>
          <p>
            Let us help you stay on top of your onboarding tasks. You’ll also find
            team insights and helpful company resources.
          </p>
        </div>
        <div className={style.showcaseGetstarted}>
          <Link onClick={incrementState} className={style.getStartedTest}>
            Get Started
          </Link>
        </div>
      </div>
    );
  }
  if (count === 1) {
    return <ShowcaseTodo style={style}/>
  }
};

export default Showcase;
