import React from 'react'


const DisplayPackages = (props)=>
   ( 
   <div style={{backgroundColor:"white",margin:'10px',width:'30%',textAlign:'center'}} >
        <div>
            <img />
        </div>

        <div>
          <p style={{fontWeight:'bold',display:'block'}}>Package Name:</p><p>{props.item.package_name}</p>
          <p style={{fontWeight:'bold',display:'block'}}>Amount:</p><p>{props.item.amount}</p>
        </div>

        <div>
            <a style={{color:'blue',paddingLeft:'20px'}} href={props.id} onClick={(e)=>props.update(e)}>update</a> 
            <a style={{color:'red',paddingLeft:'20px'}} href={props.id} onClick={(e)=>props.delete(e,props.index)}>delete</a>
        </div>
        
       
    </div>
    
    )



export default DisplayPackages