import React from "react";
import cssStyle from "../style.css";
import ShowcaseWelcome from "./components/showcase.js";
import Pageone from './components/onboarding/pageone'

const welcomePage = () => {
  return (
    <div>
      <div className={cssStyle.container}>
        <div className={cssStyle.leftPane}>
          <div className={cssStyle.logoDiv}>
            <p className={cssStyle.logo}>Logo</p>
          </div>
        </div>
        <div className={cssStyle.rightPane}>
          <div className={cssStyle.profileDiv}>
            <div className={cssStyle.bell}></div>
            <div className={cssStyle.profilePix}></div>
            <div className={cssStyle.profileName}>
              <h5>William</h5>
            </div>
          </div>
          <ShowcaseWelcome style={cssStyle} />
          {/* <Pageone style={cssStyle}/> */}
        </div>
      </div>
    </div>
  );
};

export default welcomePage;
