import React, {useState} from 'react';
import Resource from "../resource";
import PageFive from './pagefive';


const PageFour = ({style}) => {
  const [count, setCount] = useState(0);
  const increamentState = () => {
    setCount(count + 1);
  };
  if (count === 0) {
    return ( 
      <div>
      <div className={style.onboardingDiv}>
        <div className={style.HeaderText}>
          <p className={style.payroll}>Payroll</p>
          <h6>4/6</h6>
        </div>

        <div className={style.formDiv}>
          <form>
            <div className={style.formInput}>
              <label for="name">Bank Name</label>
              <input type="text" name="name" />
            </div>
            <div className={style.formInput}>
              <label for="acc">Bank Account Number</label>
              <input type="number" name="acc" />
            </div>
            <div className={style.formInput}>
              <label for="street">Tax Payer ID</label>
              <input type="text" name="street" />
            </div>
            <div className={style.formInput}>
              <label for="city">Pension Remitance ID</label>
              <input type="text" name="city" />
            </div>
            <div className={style.formInput}>
              <label for="phone">Insurance Policy Number</label>
              <input type="text" name="phone" />
            </div>
            <input
              onClick={increamentState}
              className={style.formButton}
              type="submit"
              value="Submit"
            />
          </form>
        </div>
      </div>
      <Resource style={style} />
    </div>
     );
  }
  if (count === 1) {
    return <PageFive style={style} />
  }

}
 
export default PageFour;