import React, {useState} from 'react';
import Resource from "../resource";
// import FileList from './drag and drop/dragTest';



const PageSix = ({style}) => {
  const [count, setCount] = useState(0);
  const increamentState = () => {
    setCount(count + 1);
  };
  const handleSubmit = (e)=>{
    e.preventDefault();
    alert(
      `Selected file - ${fileRef.current.files[0].name}`
      );
  }
  const spanClick = ()=> {
    fileRef.current.click();
  }

  const fileRef = React.createRef();

  if (count === 0) {
    return ( 
      <div>
      <div className={style.onboardingDiv}>
        <div className={style.HeaderText}>
          <p className={style}>Upload Documents</p>
          <h6>6/6</h6>
        </div>

        <div id={style.parentArea}>
          <form onSubmit={handleSubmit} className="my-form">
            <div className={style.fileInputDiv}>
              <p>Document 1</p>
              <div className={style.dropArea}>
                <p>Drop your file here</p><span>or</span>
                <span onClick={spanClick} className={style.labelDiv}>
                  <label>Browse</label>
                </span>
                <input ref={fileRef} type="file" id="fileUp" />
              </div>
            </div>
            <div className={style.fileInputDiv}>
              <p>Document 2</p>
              <div className={style.dropArea}>
                <p>Drop your file here</p><span>or</span>
                <span onClick={spanClick} className={style.labelDiv}>
                  <label>Browse</label>
                </span>
                <input ref={fileRef} type="file" id="fileUp" />
              </div>
            </div>
            <div className={style.fileInputDiv}>
              <p>Document 3</p>
              <div className={style.dropArea}>
                <p>Drop your file here</p><span>or</span>
                <span onClick={spanClick} className={style.labelDiv}>
                  <label>Browse</label>
                </span>
                <input ref={fileRef} type="file" id="fileUp" />
              </div>
            </div>
            <div className={style.fileInputDiv}>
              <p>Document 4</p>
              <div className={style.dropArea}>
                <p>Drop your file here</p><span>or</span>
                <span onClick={spanClick} className={style.labelDiv}>
                  <label>Browse</label>
                </span>
                <input ref={fileRef} type="file" id="fileUp" />
              </div>
            </div>
            <div>
                <input
                  className={ style.fileSubmit}
                  type="submit"
                  name="submit"
                  value="Submit"
                  onClick={increamentState}
                />
              </div>
          </form>
        </div>

      </div>
      <Resource style={style} />
    </div>
     );
  }
  if (count === 1) {
    // return <FileList />
  }

}
 
export default PageSix;