import React,{Component} from 'react';
import {apiUrl} from '../../../../reusables/endpoints';
import Staffs from '../staffs/Staffs';
import classes from './Tier1.css';



class Tier1 extends Component {


    state={
        staffs:[]
    }

    componentDidMount=()=>{
            console.log('hey')


    let Url = apiUrl()

   let companyDetails = JSON.parse(localStorage.getItem('company_details'));

   let company_id = companyDetails[0].data.id;


   console.log(company_id)

    let oldStaffs = [...this.state.staffs]

    fetch(`${Url}/v1/api/staffs/company/${company_id}`)
        .then(res => res.json())
        .then(response =>{
            console.log(response.data)
            this.setState({
                staffs:oldStaffs.concat(response.data)
            }) 
            
        })
        
    }

    render(){
        return(
            <div>

                <table className={classes.table}>
                         <div>
                            <tr>
                                <th>Name</th>
                                <th>Role</th>
                                <th></th>
                                
                            </tr>

                         </div>
                        
                          <div>
                             {this.state.staffs.map(staff=><Staffs key={staff.staff_id} name={staff.firstname+' '+staff.middlename+' '+staff.lastname} role={staff.role} id={staff.staff_id}/>)}
                          </div>

                        
                   
                    
                        
                         
                    
                </table>

            </div>
        )
    }
}


export default Tier1