import React, { useState } from "react";
import Resource from "../resource";
import PageTwo from "./pagetwo";

const PersonalDetailsOne = ({ style }) => {
  const [count, setCount] = useState(0);

  const increamentState = () => {
    setCount(count + 1);
  };

  if (count === 0) {
    return (
      <div>
        <div className={style.onboardingDiv}>
          <div className={style.HeaderText}>
            <p>Personal Details</p>
            <h6>1/6</h6>
          </div>

          <div className={style.formDiv}>
            <form>
              <div className={style.formInput}>
                <label for="name">Full name</label>
                <input type="text" name="name" />
              </div>
              <div className={style.formInput}>
                <label for="email">Email</label>
                <input type="email" name="email" />
              </div>
              <div className={style.formInput}>
                <label for="phone">Phone Number</label>
                <input type="number" pattern="[0-9]*" name="phone" />
              </div>
              <div className={style.formInput}>
                <label for="street">Street Address</label>
                <input type="text" name="street" />
              </div>
              <div className={style.formInput}>
                <label for="city">City</label>
                <input type="text" name="city" />
              </div>
              <input
                onClick={increamentState}
                className={style.formButton}
                type="submit"
                value="Next"
              />
            </form>
          </div>
        </div>
        <Resource style={style} />
      </div>
    );
  }

  if (count === 1) {
    return <PageTwo style={style}/>
  }
};

export default PersonalDetailsOne;
