import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import classes from "./RegistrationPage.css";
import { Link } from "react-router-dom";
import Form from "../../components/Forms/Form/Form";
import Input from "../../components/Forms/Input/Input";
import Button from "../../components/Forms/Button/Button";

class RegistrationPage extends Component {
  state = {
    company_info: {
      company_name: "",
      company_email: "",
    },
    display_link: false,
    company_id: 0,
    doesCompanyExist: "",
  };

  company_details = {
    name: [],
    email: [],
  };
  onChangeHandler = (e) => {
    let name;
    let email;

    e.target.type === "text"
      ? this.company_details.name.push(e.target.value)
      : (name = "");
    e.target.type === "email"
      ? this.company_details.email.push(e.target.value)
      : (email = "");

    console.log(this.company_details.name, this.company_details.email);

    let nameArrLength = this.company_details.name.length;
    let emailArrLength = this.company_details.email.length;

    let data = {
      company_name: this.company_details.name[nameArrLength - 1],
      company_email: this.company_details.email[emailArrLength - 1],
    };

    //find if company name already exist on every keystroke
    fetch("https://api-remi-hr.herokuapp.com/v1/api/companies/search", {
      method: "POST",
      body: JSON.stringify(data),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((response) => {
        console.log(response);
        if (response.message === "company does not exist") {
          this.setState({
            doesCompanyExist: "no",
          });
        } else if (response.message === "Company found") {
          this.setState({
            doesCompanyExist: "yes",
          });
        }
      })
      .catch((err) => console.log(err));

    this.setState({
      company_info: {
        company_name: this.company_details.name[nameArrLength - 1],
        company_email: this.company_details.email[emailArrLength - 1],
      },
    });
  };

  submitFormHandler = (e) => {
    e.preventDefault();

    fetch("http://localhost:5000/V1/api/companies", {
      method: "POST",
      body: JSON.stringify(this.state.company_info),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((response) => {
        console.log(response);

        this.setState({
          display_link: true,
          company_id: response.id,
        });
      })
      .catch((err) => console.log(err));
    this.props.history.push("/welcome");
  };

  render() {
    let notification;

    if (this.state.doesCompanyExist === "yes") {
      notification = <p>Company name exists</p>;
    } else if (this.state.doesCompanyExist === "no") {
      notification = <p></p>;
    }
    console.log(this.state.doesCompanyExist);
    return (
      <div className={classes.Background}>
        <div className={classes.Container}>
          <div className={classes.Left}>
            <h1>Register Company</h1>
            <h4>Create your Company account</h4>
          </div>
          {console.log(this.props)}
          {console.log(window.location.origin)}
          <div className={classes.Right}>
            <p>
              <Link to="/">Go Home</Link>
            </p>

            <h3>[Company Name]</h3>
            <Form submit={(e) => this.submitFormHandler(e)}>
              <Input
                type="text"
                placeholder="company name"
                change={(e) => this.onChangeHandler(e)}
              />
              <Input
                type="email"
                placeholder="company email"
                change={(e) => this.onChangeHandler(e)}
              />
              <p
                style={{
                  margin: 0,
                  padding: 0,
                  color: "#65B765",
                  paddingLeft: "172px",
                  display: "inline",
                }}
              >
                Login Instead
              </p>
              <Button submit={(e) => this.submitFormHandler(e)}>
                Register
              </Button>
            </Form>
            {notification}
            {this.state.display_link ? (
              <p>{`company link:${
                window.location.origin +
                "/login/" +
                this.state.company_info.company_name
              }`}</p>
            ) : (
              ""
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(RegistrationPage);
