import React from "react";

const Resource = ({ style }) => {
  return (
    <div className={style.resources}>
      <div className={style.resourcesHeader}>
        <p>Resource</p>
      </div>
      <div className={style.resourcesChild}>
        <div className={style.resourcesChildDiv}>
          <p>Company induction video</p>
        </div>
        <div className={style.resourcesChildLines}></div>
        <div className={style.resourcesChildDiv}>
          <p>Company ethics</p>
        </div>
        <div className={style.resourcesChildLines}></div>
        <div className={style.resourcesChildDiv}>
          <p>Terms and Agreement</p>
        </div>
        <div className={style.resourcesChildLines}></div>
        <div className={style.resourcesChildDiv}>
          <p>Resource 4</p>
        </div>
        <div className={style.resourcesChildLines}></div>
      </div>
    </div>
  );
};

export default Resource;
